.haveSomeInformation {
  position: absolute;
  top: 207px;
  left: 45px;
  font-size: var(--font-size-xs);
  line-height: 16px;
  display: inline-block;
  width: 190px;
}
.select {
  position: relative;
  font-size: var(--font-size-xs);
  line-height: 10.38px;
  font-weight: 600;
  font-family: var(--font-raleway);
  color: var(--color-gray-300);
  text-align: center;
}
.selectWrapper {
  cursor: pointer;
  border: 0;
  padding: var(--padding-3xs) 0;
  background-color: var(--color-white);
  position: absolute;
  top: 279px;
  left: 78px;
  border-radius: var(--br-9xs);
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  width: 125px;
  height: 32px;
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  align-items: center;
  justify-content: flex-start;
}
.frameChild {
  position: absolute;
  height: 5.54%;
  width: 39.29%;
  top: 39.94%;
  right: 30.36%;
  bottom: 54.52%;
  left: 30.36%;
  border-radius: 50%;
  background-color: var(--color-gray-500);
  filter: blur(24px);
}
.blogging1Icon {
  position: absolute;
  top: 0;
  left: 55px;
  width: 170px;
  height: 170px;
  object-fit: cover;
}
.textToVideoParent {
  position: relative;
  border-radius: var(--br-5xs);
  background: linear-gradient(246.64deg, #f6b3b3, #683ee1);
  width: 280px;
  height: 343px;
  text-align: center;
  font-size: var(--font-size-base);
  color: var(--color-white);
  font-family: var(--font-raleway);
}
