@import url("https://fonts.googleapis.com/css2?family=Raleway:wght@300;400;600;700;800&display=swap");
body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --font-raleway: Raleway;
  --font-inherit: inherit;

  /* font sizes */
  --font-size-xs: 12px;
  --font-size-base: 16px;
  --font-size-5xl: 24px;
  --font-size-sm: 14px;

  /* Colors */
  --color-gray-100: #797979;
  --color-gray-200: #222526;
  --color-gray-300: #242424;
  --color-gray-400: #1f2125;
  --color-gray-500: rgba(0, 0, 0, 0.5);
  --color-darkgray: #959798;
  --color-darkslategray-100: #424242;
  --color-darkslategray-200: #393939;
  --color-white: #fff;

  /* Gaps */
  --gap-5xs: 8px;

  /* Paddings */
  --padding-2xl: 21px;
  --padding-3xs: 10px;

  /* border radiuses */
  --br-9xs: 4px;
  --br-5xs: 8px;
}
